.title {
  margin: 0 0 32px;

  font-weight: bold;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  letter-spacing: -1.5px;
  color: #000;

  @include onDesktop {
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 56px;
  }

  &::after {
    content: "";
    display: block;
    width: 23px;
    height: 16px;
    margin: 4px auto 0;
    background-image: url(../images/crown.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.paragraph-2 {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 27px;
}

.paragraph-bolt {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 27px;
}

.subtitle {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
}
